import Vue from 'vue'
import VueRouter from 'vue-router'
import store from '@/store'

Vue.use(VueRouter);

const getTask = function(route) {
  return {
    task: store.getters.GET_TASK_BY_ID(route.params.id)
  };
};

const routes = [
  {
    path: '/',
    name: 'List',
    component: () => import('@/views/List.vue')
  },
  {
    path: '/task/:id',
    name: 'Task',
    component: () => import('@/views/Task.vue'),
    props: getTask,
    beforeEnter: (to, from, next) => {
      const task = store.getters.GET_TASK_BY_ID(to.params.id);
      
      if (task) {
        next();
      } else {
        next({
          name: "List"
        });
      }
    }
  }
]

const router = new VueRouter({
  mode: 'history',
  base: process.env.BASE_URL,
  routes
})

export default router
